import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { useParams } from 'react-router-dom';

function Results() {
  const { level } = useParams()
  const url = `/api/monsters/create`
  const [product, setProduct] = useState(null);
  let content = null;



  useEffect(() => {
    axios.post(url, { level }).then((res) => {
      setProduct(res.data);
    })
  }, [url])

  if (product) {
    let list = product.map((m) => (
      <tr
        className="border-b"
      >
        <td className="border-r border-l px-3 py-1"><h1 className="font-medium">{m.name.charAt(0).toUpperCase() + m.name.slice(1)}</h1></td>
        <td className="border-r px-3 py-1">{m.hp}</td>
        <td className="border-r px-3 py-1">{m.patk}</td>
        <td className="border-r px-3 py-1">{m.matk}</td>
        <td className="border-r px-3 py-1">{m.pdef}</td>
        <td className="border-r px-3 py-1">{m.mdef}</td>
        <td className="border-r px-3 py-1">{m.gold}</td>
      </tr>
    )
    )
    content = (
      <div>
        <table className="mx-auto my-6">
          <thead className="border-b border-t">
            <th className="border-r border-l px-3 py-1">Name</th>
            <th className="border-r px-3 py-1">HP - ‚ù§Ô∏è</th>
            <th className="border-r px-3 py-1">Attack Damage - ‚öîÔ∏è</th>
            <th className="border-r px-3 py-1">Magic Damage - üîÆ</th>
            <th className="border-r px-3 py-1">Attack Defence - üõ°Ô∏è</th>
            <th className="border-r px-3 py-1">Magic Defence - üßø</th>
            <th className="border-r px-3 py-1">Gold - üí∞</th>
          </thead>
          <tbody>{list}</tbody>

        </table>
      </div>
    );
  }
  return (
    <div
      className="text-center"
    >
      <h1 className="font-bold p-3">Monsters</h1>
      { content}
    </div>
  )
}

export default Results