import Header from './components/Header';
import Footer from './components/Footer';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from 'react-router-dom'
import Home from './Views/Home'
import Results from './Views/Results'
function App() {
  let brand = "Donjons and Wyrms"
  return (
    <div>
      <Router>
        <Header brand={brand} />
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/results/:level">
            <Results />
          </Route>
        </Switch>
        <Footer />
      </Router>
    </div>
  );
}

export default App;
