import { React, useState } from 'react'
import { Link } from 'react-router-dom';

function Home() {
  const [selected, setSelected] = useState(1);
  return (
    <div className="text-center">
      <h1 className="font-bold text-2xl p-3">Monster Creation Page üéÉ</h1>
      <form action="" className="border-2 inline-block p-3 rounded-md mt-3">
        <div>
          <label
            htmlFor="levelInput"
            className="p-3"
          >Enter Level</label>
          <select
            name="levels"
            id="levelInput"
            onChange={(e) => setSelected(e.target.value)}
            className="py-2 w-40 px-3 items-center border rounded-md shadow-none inline-flex text-base h-10 justify-start"
          >
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
            <option value="5">5</option>
            <option value="6">6</option>
            <option value="7">7</option>
            <option value="8">8</option>
            <option value="9">9</option>
            <option value="10">10</option>
          </select>
        </div>
        <div>
          <button
            className="border mt-3 rounded p-1 bg-blue-400 text-blue-50 hover:shadow-md hover:border-black"
          >
            <Link
              to={`/results/${selected}`}
            >Create Monsters</Link>

          </button>
        </div>
      </form>
    </div>
  )
}

export default Home