import {
  Link
} from 'react-router-dom'
function NavigationMenu(props) {
  return (
    <div>
      <span className="font-bold px-3 sm:hidden">{props.brand}</span>
      <Link
        to="/"
        className="text-blue-500 px-3 border-l"
        onClick={props.closeMenu}
      >
        Home
          </Link>
    </div>
  );

}

export default NavigationMenu